const mapStateToProps = state =>{
    return{
        data: state.data
    }
}

const mapActionToProps = dispatch =>{
    return{
        updatemovies:(data)=> dispatch({type:"updatemovies",data:data})
    }
}
export default connect(mapStateToProps,mapActionToProps)(HomePage)